export class OnsiteItem {
    constructor(
        public img: String,
        public title: String,
        private _status: number,
        public summary: String,
        public date: Date,
        public list: OnsiteItemStatus[]
    ) {}

    get status () {
        return this.list[this._status - 1].content;
    }

    get color () {
        return this.list[this._status - 1].color;
    }

    get datestr () {
        const now = new Date();
        if (now.getFullYear() === this.date.getFullYear()) {
            return (this.date.getMonth() + 1) + '' + this.date.getDate() + '';
        }else {
            return this.date.getFullYear() + '-' + (this.date.getMonth() + 1) + '-' + this.date.getDate();
        }
    }
}
class OnsiteItemStatus {
    constructor(
        public id: Number,
        public content: String,
        public color: String = '#0099CC') {}
}
export class OnsiteStatus {
    /**
     * 
     */
    public static success = 1;
    /**
     * 
     */
    public static draft = 2;
    /**
     * 
     */
    public static onsite = 3;
    /**
     * 
     */
    public static internal = 4;
    /**
     * 
     */
    public static reject = 5;
    /**
     * 
     */
    public static invalid = 6;
    /**
     * status, 
     */
    public static status = [
        new OnsiteItemStatus(1, '', '#259b24'),
        new OnsiteItemStatus(2, '', '#e51c23'),
        new OnsiteItemStatus(3, '', '#ff9800'),
        new OnsiteItemStatus(4, '', '#ff9800'),
        new OnsiteItemStatus(5, '', '#e51c23'),
        new OnsiteItemStatus(6, '', '#e51c23')
      ];
}



