import { Injectable } from '@angular/core';
import { MenuItem } from '../../class/menu-item';

@Injectable()
export class DingtalkService {
  private corpid: String = '';

  constructor() { }

  setCorpid(corpid: String) {
    this.corpid = corpid;
  }

  /**
   * 
   * @param title 
   */
  setTitle(title: String) {
    dd.ready(function(){
      dd.biz.navigation.setTitle({
        title : title,
        onSuccess : function(result) {
        },
        onFail : function(err) {}
      });
    });
  }

  /**
   * 
   */
  share() {
    dd.ready(function() {
      dd.biz.util.share({
        style: 0,
        url: 'http://' + window.location.host,
        onSuccess : function(res) {},
        onFail : function(err) {}
      });
    });
  }

  showPreloader(text: string = '',
    showIcon: boolean = true,
    onSuccess?: Function,
    onFail?: Function) {
    dd.ready(function() {
      dd.device.notification.showPreloader({
        text: text,
        showIcon: true,
        onSuccess : function(result) {
          onSuccess(result);
        },
        onFail : function(err) {
          onFail(err);
        }
      });
    });
  }

  hidePreloader(onSuccess?: Function, onFail?: Function) {
    dd.ready(function() {
      dd.device.notification.hidePreloader({
        onSuccess : function(result) {
          onSuccess(result);
        },
        onFail : function(err) {
          onFail(err);
        }
      });
    });
  }

  /**
   * toast
   * @param text 
   * @param icon iconsuccesserror 0.0.2
   * @param duration [android(<=2s >2s)]
   * @param delay 0
   */
  toast(text: String,
    icon: String = '',
    onSuccess?: Function,
    onFail?: Function,
    duration: Number = 1,
    delay: Number = 0) {
    dd.ready(function(){
      dd.device.notification.toast({
        icon: 'success',
        text: text,
        duration: 1,
        delay: 0,
        onSuccess : function(result) {
          onSuccess(result);
        },
        onFail : function(err) {
          onFail(err);
        }
      });
    });
  }

  /**
   * confirm
   */
  setNavigationConfirm(message: string,
    onSuccess?: Function,
    onFail?: Function,
    title: string = '',
    buttonLabels: String[] = ['', '']) {
      dd.ready(function(){
        dd.device.notification.confirm({
          message: message,
          title: title,
          buttonLabels: buttonLabels,
          onSuccess : function(result) {
            onSuccess(result);
          },
          onFail : function(err) {
            onFail(err);
          }
        });
      });
  }

  /**
   * 
   */
  goBack(onSuccess?: Function, onFail?: Function) {
    dd.ready(function(){
      dd.biz.navigation.goBack({
        onSuccess : function(result) {
          onSuccess(result);
        },
        onFail : function(err) {
          onFail(err);
        }
      });
    });
  }

  /**
   * 
   * @param onSuccess 
   */
  setNavigationLeft(onSuccess?: Function, onFail?: Function) {
    dd.ready(function(){
      // 
      if (dd.ios) {
        dd.biz.navigation.setLeft({
          control: true,
          text: '',
          onSuccess : function(result) {
            onSuccess(result);
          },
          onFail : function(err) {
            onFail(err);
          }
        });
      }else if (dd.android) {
        document.addEventListener('backbutton', function(e) {
          onSuccess(e);
          e.preventDefault();
        });
      }
    });
  }

  setNavigationMenu(items: MenuItem[],
    onSuccess: Function,
    onFail?: Function,
    backgroundColor?: String,
    textColor?: String) {
    dd.ready(function(){
      dd.biz.navigation.setMenu({
  items : items,
  onSuccess: function(data) {
    onSuccess(data);
  },
  onFail: function(err) {
            onFail(err);
  }
  });
    });
  }

  /**
   * 
   * @param text 
   * @param show  true  false  false
   * @param control true false  false
   * @param onSuccess controltrueonSuccess
   * @param onFail 
   */
  setNavigationRight(text: string = '',
    show: boolean = false,
    control: boolean = false,
    onSuccess?: Function,
    onFail?: Function) {
    dd.ready(function(){
      dd.biz.navigation.setRight({
    show: show,
        control: control,
        text: text,
    onSuccess : function(result) {
          onSuccess(result);
    },
    onFail : function(err) {
          onFail(err);
        }
      });
    });
  }
}



