export class User {

    constructor(id: Number,
        public nickName: String,
        public  isAvatar: Boolean,
        public  avatar: String,
        public  phoneNumber: String,
        public  character: String = '',
        public  job: String = '') {
    }

    nameToString(): String {
        return '' + this.nickName;
    }
}



