import { Injectable } from '@angular/core';
import { OnsiteItem, OnsiteStatus } from '../../class/onsite-item';

@Injectable()
export class WorkService {
  private onsiteList: OnsiteItem[];

  constructor() {
    this.onsiteList = [
      new OnsiteItem('https://modao.cc/uploads3/images/1275/12759365/raw_1505547729.jpeg',
        '35#1-50#', OnsiteStatus.onsite, '',
        new Date(2017, 8 , 16), OnsiteStatus.status),
      new OnsiteItem('https://modao.cc/uploads3/images/1275/12759572/raw_1505548205.jpeg',
        '', OnsiteStatus.draft, '',
        new Date(2017, 8 , 2), OnsiteStatus.status),
      new OnsiteItem('https://modao.cc/uploads3/images/1275/12759590/raw_1505548242.jpeg',
        'YJ140T-6', OnsiteStatus.internal, '',
        new Date(2017, 7 , 20), OnsiteStatus.status),
      new OnsiteItem('https://modao.cc/uploads3/images/1275/12759608/raw_1505548276.jpeg',
        '', OnsiteStatus.success, '',
        new Date(2017, 6 , 11), OnsiteStatus.status),
      new OnsiteItem('https://modao.cc/uploads3/images/1275/12759630/raw_1505548320.jpeg',
        '1#B', OnsiteStatus.success, '',
        new Date(2016, 11 , 30), OnsiteStatus.status)
    ];
    this.onsiteList = this.onsiteList.concat(this.onsiteList);
  }

  getOnsiteList(callback: Function) {
    callback(this.onsiteList);
  }

}



